.cartitems{
    margin: 100px 170px;
}

.cartitems hr {
    height: 3px;
    background: #e2e2e2;
    border: 0;
}

.cartitems-format-main{
    display: grid;
    grid-template-columns:0.5fr 2fr 1fr 1fr 1fr 1fr ;
    align-items: center;
    gap: 75px;
    padding: 20px 0px;
    color: #454545;
    font-size: 15px;
    font-weight: 500;
}

.cartitems-format {
    font-size: 15px;
    font-weight: 500;
  

}

.cartitems-format img {
    width: 50%;
    border-radius: 20px;
}

.cartitems-quantity{
    width: 60px;
    height: 50px;
    border: 2px solid #ebebeb;
    background: #fff;
}

.cartitems-down {
    display: flex;
    margin: 100px 0px;
}
.cartitems-total {
    flex: 1;
    display: flex;
    flex-direction: column;
    margin-right: 200px;
    gap: 40px;
}

.cartitems-total-item {
    display: flex;
    justify-content: space-between;
    padding: 15px 0px;
}

.cartitems-total button {
    width: 200px;
    height: 50px;
    outline: none;
    background: red;
    border-radius: 10px;
    cursor: pointer;
}

.cartitems-promocode{
     flex: 1;
     font-size: 15px;
     font-weight: 500;
}

.cartitems-promocode p {
    color: #555;
}

.cartitems-promobox {
    width: 500px;
    margin-top: 15px;
    padding-left: 50px;
    background: #eaeaea;
}

.cartitems-promobox input {
    border: none;
    outline: none;
    background: transparent;
    font-size: 15px;
    width: 300px;
    height: 50px;
}

.cartitems-promobox button {
    width: 100px;
    height: 60px;
    background: #454545;
    color: white;
    cursor: pointer;
    
}